import Table from "../Components/Table";
import Searchbox from "../Components/Searchbox";
import { Component } from 'react';
import axios from 'axios';

class Directory extends Component {

        state = {
            users: [{}],
            filteredUsers: [{}]
        }
    
    componentDidMount() {
        axios.get("https://randomuser.me/api/?results=25&nat=us").then((res) => {
            this.setState({
                users: res.data.results,
                filteredUsers: res.data.results
            })
        })
    }

    handleSearchChange = event => {
        console.log(event.target.value);
        const filter = event.target.value;
        const filteredList = this.state.users.filter(user => {
            let values = Object.values(user).join("").toLowerCase();
            return values.indexOf(filter.toLowerCase()) !== -1;
        })
        this.setState({filteredUsers: filteredList});
        console.log(this.state.filteredUsers);
    }

    render() {
        console.log(this.state);

        return (
            <div>
                <Searchbox handleSearchChange={this.handleSearchChange}/>
                <Table users={this.state.filteredUsers}/>
            </div>
        )
    }
}

export default Directory;